.App {
  text-align: center;
}

.App p {
  margin-top: 40px;
  font-size: calc(10px + 4vmin);
  color: #282c34;
}

.App-logo {
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    width: 40%;
    height: 40%;
    animation: App-logo-spin infinite 10s linear;
  }
}

.App-header {
  background-color: white;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 4vmin);
  color: #282c34;
}

.App-text {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: #282c34;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg) scale(0.1);
  }
  17% {
    transform: rotate(120deg) scale(0.7);
  }
  34% {
    transform: rotate(240deg) scale(0.1);
  }
  51% {
    transform: rotate(360deg) scale(0.7);
  }
  67% {
    transform: rotate(480deg) scale(0.1);
  }
  85% {
    transform: rotate(600deg) scale(0.7);
  }
  100% {
    transform: rotate(720deg) scale(0.1);
  }
}
